.shopcategory-banner{
    display: block;
    margin: 30px auto;
/* width: 82%; */
}
.shopcategory-indexsort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
    padding: 10px;
    border-radius: 40px;
    border: 1px solid grey;

}
.shopcategory-products{
    margin: 20px 170px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.shopcategory-load{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    background: black;
    color: #787878;
    border-radius: 50px;
}