.productdisplay{
    display: flex;
    margin: 0px 170px;

}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    height: 100px;
}
.productdisplay-main-img{
    width: 500px;
    height: 500px;
}
.productdisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;

}
.productdisplay-right h1{
    color: #3d3d3d;
}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
}
.productdisplay-right-prices{
    display: flex;
    margin: 40px 0px;
    gap: 30px;

}
.productdisplay-right-prices-old{
    text-decoration: line-through;
color: #818181
}
.productdisplay-right-prices-new{
    color:red ;
}
.productdisplay-right-size h1{
    margin-top: 55px;
    color: #656565;
    font-size: 20px;

}
.productdisplay-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    padding: 15px 20px;
    background-color: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    color: white;
    background: red;
    margin-bottom: 30px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
}
