.cartitem{
    margin: 100px 170px;
}
.cartitem hr{
    height: 3px;
    border: 0;
background: grey;

}
.cartitem-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    padding: 20px 0px;
    gap: 75px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitem-format{
    font-size: 17px;
    font-weight: 800;
}
.carticon-product-icon{
    height: 150px;
}
.cartitems-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}
.cartitem-quantity{
    width: 64px;
    height: 50px;
    border:  2px solid #ebebeb;
}
.cartitem-down{
    display: flex;
    margin: 100px 0px;
}
.cartitem-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}
.cartitem-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitem-total button{
    width: 262px;
    height: 58px;
    border: none;
    background-color: #ff5a5a;
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitem-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitem-promocode p{
   color: #555;
}
.cartitem-promobox{
    width: 504px;
    margin-top: 15px;
    padding-left:20px;
    height: 58px;
    background-color: #eaeaea;
}
.cartitem-promobox input{
    border: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px ;
}
.cartitem-promobox button{
    width: 150px;
    height: 58px;
    font-size: 16px;
    background-color: black;
    color: white;
    cursor: pointer;
}